import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MenuController, AlertController } from '@ionic/angular';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { RecoveryDTO } from '../models/user/recovery.dto';
import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-recovery',
  templateUrl: './recovery.page.html',
  styleUrls: ['./recovery.page.scss'],
})
export class RecoveryPage implements OnInit {

  formGroup: FormGroup;
  formSolicitacao: FormGroup;
  recovery: RecoveryDTO;

  loading: boolean = false;

  display: boolean = true;

  constructor(private router: Router,
    private menu: MenuController,
    public formBuilder: FormBuilder,
    private authService: AuthService,
    private alertCtrl: AlertController) {

    this.formSolicitacao = this.formBuilder.group({
      email: ['lhccorrea@hotmail.com', [Validators.required, Validators.email]]
    });

    this.formGroup = this.formBuilder.group({
      codigoRecuperacao:  ['', [Validators.required]],
      senha: ['', [Validators.required, Validators.minLength(8)]],
      senhaR: ['', [Validators.required, Validators.minLength(8)]]
    });

  }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.menu.enable(false)
  }

  solicitar() {
    this.loading = true;
    
    const email = this.formSolicitacao.get('email').value;
    this.authService.requestRecovery(email).
    subscribe(res => {
      console.log(res);
      this.loading = false;
      this.display = false;
      this.showRequestOk();
    },
      error => { 
        this.loading = false;
        this.display = true;
        console.log(error);
      });
  }

  registrar() {
    this.loading = true;

    this.recovery = {
      email: this.formSolicitacao.get('email').value,
      codigoRecuperacao: this.formGroup.get('codigoRecuperacao').value,
      senha: this.formGroup.get('senha').value,
    }

    this.authService.doRecovery(this.recovery).subscribe(res => {
      this.loading = false;
      this.showOk();
    },
      error => { this.loading = false });
  }

  async showRequestOk() {
    let alert = await this.alertCtrl.create(
      {
        header: 'Sucesso!',
        message: 'Enviado e-mail com código de recuperação',
        backdropDismiss: false,
        buttons: [
          {
            text: 'OK',
            handler: () => {}
          }
        ]
      }
    );

    alert.present();
  }

  async showOk() {
    let alert = await this.alertCtrl.create(
      {
        header: 'Sucesso!',
        message: 'Senha alterada com sucesso!',
        backdropDismiss: false,
        buttons: [
          {
            text: 'OK',
            handler: () => { this.router.navigateByUrl('login'); }
          }
        ]
      }
    );

    alert.present();
  }

  getValid() {
    return !(this.formGroup.valid && this.getSenhas());
  }

  getSenhas(){
    return this.formGroup.get('senha').value == this.formGroup.get('senhaR').value;
  }

}
